import React from 'react';
import '../IdCard/IdCard.css';

const IdCard = ({url1,url2}) => {
  return (
    <div className='_id_card_main'>
        <div className='idCardImage_main_div'>
            <img src={url1} alt='image' className='IDCard_image_css' />
        </div>
        <div className='profile_picture_main_div'>
            <img src={url2} alt='image' className='Profile_picture_image_css' />
        </div>
    </div>
  )
}

export default IdCard